import React from "react";
import TooltipFromHtml from "eshop/modules/core/components/ui/TooltipFromHtml";

const OfferDisclaimerComponent = ({descriptions, processType, className}) => {
    const offerDisclaimer = descriptions[['offerDisclaimer', processType].filter(Boolean).join(".")];
    return !!offerDisclaimer ?
        <TooltipFromHtml className={`g-black1-bg g-white1-c u-padding-all-l u-padding u-padding-top ${className}`}
             html={offerDisclaimer}/> :
        null
};

export default OfferDisclaimerComponent;